body {
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin: 0;
    padding: 0;
    background-color: #f4f4f4;
}

h1 {
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    margin-top: 20px;
    color: #333;
}

#input-pesquisa{
    padding: 10px;
    padding-inline: 10%;
    border-radius: 5px;
    border: none;
    margin-right: 20px;
}

#inputs_addcar {
    display: flex;
    flex-wrap: wrap;
    gap: 10px;
    justify-content: center;
    padding: 10px;
    margin-inline: 5%;
}

#inputs_addcar input {
    flex: 1 1 calc(25% - 10px);
    min-width: 150px;
    padding: 10px;
    background-color: rgb(172, 172, 172);
    color: white;
    border: none;
    border-radius: 5px;
    font-size: 14px;
}

#inputs_addcar input::placeholder {
    color: white;
    opacity: 0.8;
}

#inputs_addcar button {
    flex: 1 1 40%;
    padding: 10px;
    font-size: 16px;
    border: none;
    background-color: #007bff;
    color: white;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s ease;
}

#inputs_addcar button:hover {
    background-color: #0056b3;
}

