table {
    width: 90%;
    margin: 20px auto;
    border-collapse: collapse;
    background-color: rgba(196, 196, 196, 0.507);
    border-radius: 10px;
    overflow: hidden;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

thead {
    background-color: #007bff;
    color: white;
}

thead th {
    padding: 15px;
    text-align: center;
    font-weight: bold;
}

tbody {
    max-height: 300px;
    overflow-y: auto;
    width: 100%;
}

tbody::-webkit-scrollbar {
    width: 8px;
    height: 8px;
}

tbody::-webkit-scrollbar-thumb {
    border-radius: 10px;
    background-color: rgba(185, 185, 185, 0.904);
}

tbody:hover::-webkit-scrollbar-thumb {
    visibility: visible;
}

tbody td {
    padding: 10px;
    text-align: center;
    border-bottom: 1px solid rgba(0, 0, 0, 0.1);
}

tbody tr:hover {
    background-color: rgba(255, 255, 255, 0.8);
}

tr {
    white-space: nowrap;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
}

td,
th {
    padding: 10px;
    text-align: center;
}

button {
    cursor: pointer;
    padding: 5px 10px;
    border: none;
    background-color: #007bff;
    color: white;
    border-radius: 5px;
    transition: background-color 0.3s ease;
    margin: 10px;
}

button:hover {
    background-color: #0056b3;
}

#tablecars {
    border-radius: 10px;
    background-color: rgba(196, 196, 196, 0.507);
    padding: 10px;
    margin-top: 20px;
}
